<template>
  <div class="flex items-center justify-center">
    <div class="w-full max-w-sm md:max-w-xl">
      <form class="rounded px-8 pt-6 pb-8 mb-4" @submit.prevent="registerUser">
        <div class="mb-4">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="name"
            >Name</label
          >
          <input
            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            id="name"
            type="text"
            placeholder="Name"
            v-model="name"
          />
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="email"
            >Email</label
          >
          <input
            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            id="email"
            type="email"
            placeholder="Email"
            v-model="email"
          />
        </div>
        <div class="mb-6">
          <label
            class="block text-gray-700 text-sm font-bold mb-2"
            for="password"
            >Password</label
          >
          <input
            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            id="password"
            type="password"
            placeholder="Password"
            v-model="password"
          />
        </div>
        <div class="mb-6">
          <label
            class="block text-gray-700 text-sm font-bold mb-2"
            for="passwordConfirm"
            >Confirm Password</label
          >
          <input
            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="passwordConfirm"
            type="password"
            placeholder="Password"
            v-model="passwordConfirm"
          />
        </div>
        <div class="flex items-center justify-center">
          <button
            class="bg-teal-500 hover:bg-teal-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="submit"
          >
            Sign Up
          </button>
        </div>
        <p class="mx-auto text-center text-xs mt-4">
          Already registered?
          <router-link :to="{ name: 'Login' }">
            <span class="text-teal-500">Log In</span>
          </router-link>
        </p>
        <Error />
      </form>
    </div>
  </div>
</template>

<script>
import Error from "@/components/Error";
import { mapActions } from "vuex";

export default {
  name: "Register",
  components: {
    Error
  },
  data() {
    return {
      name: "",
      email: "",
      password: "",
      passwordConfirm: ""
    };
  },
  methods: {
    ...mapActions(["register"]),
    async registerUser() {
      const userInfo = {
        name: this.name,
        email: this.email,
        password: this.password,
        passwordConfirm: this.passwordConfirm
      };
      const res = await this.register(userInfo);

      if (res.data.success) {
        this.$router.push({ name: "Login" });
      } else {
        this.email = "";
        this.password = "";
        this.passwordConfirm = "";
      }
    }
  }
};
</script>

